* {
  font-style: normal;
}

.about {
  z-index: 1;
  position: relative;
  padding: 125px 0 39px;

  background-image: url('/images/desktop/about/bckg-milk.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 80%;

  background-color: $primary-shade-color;

  @media screen and (min-width: $tablet) {
    background-image: none;
    padding: 130px 0 66px;
  }

  @media screen and (min-width: $desktop) {
    margin-bottom: 125px 0 39px;
  }
}

.about__title-label {
  margin-bottom: 15px;

  color: $main-shade-color;

  font-family: 'Titan One';
  font-weight: normal;
  font-size: 18px;
  line-height: 0.85;
  text-align: center;
  text-transform: uppercase;
}

.about__title {
  margin-bottom: 26px;
  color: $main-color;

  font-family: 'Titan One';
  font-weight: normal;
  font-size: 34px;
  line-height: 0.87;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    margin-bottom: 50px;
  }
}

.about__thumb {
  min-width: 280px;

  @media screen and (min-width: $tablet) {
    min-width: 340px;
    margin-right: 17px;
  }

  @media screen and(min-width: $desktop) {
    min-width: 516px;
    margin-right: 74px;
  }
}

.about__main-img {
  display: block;
  max-width: 100%;
  height: auto;
}

.about__description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.about__description-text-main {
  margin-bottom: 32px;

  font-family: DM Sans;
  font-weight: 500;
  font-size: 12px;
  line-height: 162%;

  color: $secondary-color;

  @media screen and (min-width: $tablet) {
    margin-bottom: 31px;

    font-size: 14px;
    line-height: 176%;
  }

  @media screen and (min-width: $desktop) {
    font-size: 16px;
    line-height: 176%;
  }
}

.about__description-text-secondary {
  margin-bottom: 21px;

  color: $secondary-shade-color;

  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 162%;

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 176%;
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: $desktop) {
    font-size: 14px;
    line-height: 194%;
  }
}

.about__button {
  padding-left: 37px;
  padding-right: 37px;
  margin-right: auto;
  color: $main-color;
  background-color: $primary-color;
  min-height: 44px;
  font-weight: bold;
  font-size: 14px;
  line-height: 0.8;
  text-align: center;
  border-radius: 22px;
  border: none;

  &:hover,
  &:focus {
    background-color: $accent-color-btn;
  }

  &::after {
    content: '';
    width: 10px;
    height: 10px;
    background-image: url(/images/btn-icon.png);
    display: inline-flex;
    background-repeat: no-repeat;
    margin-left: 15px;
    background-position: 50% 50%;
  }
}

//--планшет--//

.about__tablet {
  @media screen and (min-width: $tablet) {
    display: flex;
    align-items: flex-end;

    margin-bottom: 56px;
  }

  @media screen and(min-width: $desktop) {
    display: none;
  }
}
//--планшет--//

//--десктоп--//
.about__desktop {
  display: none;
  @media screen and (min-width: $desktop) {
    display: flex;
    align-items: center;
  }
}

.btn-remover {
  @media screen and (min-width: $desktop) {
    display: none;
  }
}
//--десктоп--//

//--модальное окно--//

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}
.modal-about {
  width: 300px;
  padding: 40px 40px;
  background-color: $primary-shade-color;
  background-image: url('/images/mobile/about/modal-icecream.png');
  background-repeat: no-repeat;
  background-position: 50% 98%;
  background-size: 60px;

  @media screen and (min-width: $tablet) {
    width: 450px;
    height: 500px;
  }
}

.modal__button {
  position: absolute;
  top: 15px;
  right: 15px;
  border: none;
  background-color: transparent;

  cursor: pointer;
}

.modal__main-title {
  margin-bottom: 35px;
  font-family: Titan One;
  font-weight: normal;
  font-size: 28px;
  line-height: 0.9;
  text-align: center;
  letter-spacing: 0.04em;
  text-align: center;
  color: $main-color;

  @media screen and (max-width: $tablet) {
    font-size: 20px;
    margin-bottom: 30px;
  }
}

.modal__subtitle {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 186%;
  letter-spacing: 0.04em;
  color: $secondary-color;

  @media screen and (max-width: $tablet) {
    font-size: 14px;
    margin-bottom: 7px;
  }
}

.modal__text {
  margin-bottom: 20px;

  font-family: 'DM Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 166%;
  color: $secondary-shade-color;

  &:last-child {
    margin-bottom: 35px;
  }
  @media screen and (max-width: $tablet) {
    font-size: 10px;
    margin-bottom: 7px;
  }
}
//--модальное окно--//

//--брызги--//

.milk-splashes1,
.milk-splashes2 {
  z-index: -1;
  @media screen and (max-width: 1199px) {
    display: none;
  }
}

.milk-splashes1 {
  position: absolute;
  top: 170px;
  left: 0;
  background-image: url('/images/desktop/about/milk-left.png');
  background-repeat: no-repeat;
  background-size: cover;
  width: 160px;
  height: 179px;
}

.milk-splashes2 {
  position: absolute;
  top: 200px;
  right: 0;
  background-image: url('/images/desktop/about/milk-right.png');
  background-repeat: no-repeat;
  background-size: cover;
  width: 268px;
  height: 391px;
}
